.border {
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: rgb(179, 72, 72);
  border-bottom: 2px solid black;
  padding: 0 2em;
}
ul {
  display: flex;
  flex-direction: row;
  margin: 1.5em;
}
li {
  display: list-item;
  margin: 0 1vh 0 1.85vh;
  font-family: sans-serif;
  color: rgb(102, 97, 97);
  list-style-type: none;
  font-size: 1em;
  cursor: pointer;
}
li:hover{
  color: rgb(179, 72, 72);
  transition: 0.5s;
}
.styles-li{
  margin: 0 1vh 0 1.85vh;
}
a{
  color: inherit;
  text-decoration: none;
  position: relative;
  display: flex;
  flex-direction: row;
  font-size: 1.2rem;
}
@media(max-width: 580px) { 
  .border{
    padding: 0 .3em;
  }
  .logo{
  font-size: 1.2em;
  
 }
 a{
  font-size: .8rem;
 }

}
