.border-container{
    width: 80%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    display: flex;
    border: 2px solid rgb(102, 97, 97);
    box-shadow: 1px 1px 1px 1px rgb(102, 97, 97);
    border-radius: 1em;
    margin: auto;
    background-color: #f1eaea;
  }
.styles-menu{
    text-align: center;
    font-size: 5vh;
    color:  rgb(179, 72, 72);
    margin-top: 5rem;
  }
  .styles-name{
    text-align: center;
    margin-top: 1.5rem;
    font-size: 2em;
    color: black;
  }
  span{
    text-align: center;
    display: flex;
  }
  .intro-img{
    max-width: 650px;
    height: 650px;
    border-radius: 100%;
    border: 2px solid black;
    overflow: hidden;
  }
.imgBorder{
  width: 100%;
}
@media (max-width: 830px) {
  .border-container{
    font-size: .8rem;
  }
  .intro-img{
    width: 90%;
    height: 80%;
  }
}